import LoginVerifyHandler from "./LoginVerifyHandler";
import HandleMgr from "./HandleMgr";
import {UserCmdModule} from "./cmd/CmdModule";

export default class RegisterMgr {

    private static _ins: RegisterMgr;

    public static get ins(): RegisterMgr {
        if (!this._ins) this._ins = new RegisterMgr();
        return this._ins;
    }

    //注册协议
    public registCmd(): void {
        HandleMgr.addHandler(UserCmdModule.MAIN_CMD, UserCmdModule.LOGIN, new LoginVerifyHandler())
    }

}
